


















import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import { getFleetList } from '@/store/modules/bus/actions'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class carInfo extends Vue {
  $createToast : any
  $refs : any
  carId : any
  dateTimePicker : any
  $createDatePicker : any
  departureTime = ''
  $route : any
  model : any = {}
  type : String = 'add'
  fields = [
    { type: 'input', modelKey: 'recruit_name', label: '职位名称', props: { placeholder: '请输入' }, rules: { required: true } },
    { type: 'textarea', modelKey: 'recruit_info', label: '职位详情', props: { placeholder: '请输入', maxlength: '3000' }, rules: { required: true } },
    { type: 'input', modelKey: 'recruit_num', label: '招聘人数', props: { placeholder: '请输入' }, rules: { required: true, type: 'number' } },
    { type: 'input', modelKey: 'recruit_wages', label: '工资', props: { placeholder: '请输入' }, rules: { required: true } }
  ]
  activated () {
    this.type = this.$route.query.type || 'add'
    if (this.type === 'edit') {
      this.model = this.$route.query.recruitInfo
    } else {
      this.model = { enterpriseId: this.$route.query.enterpriseId }
    }
  }
  deactivated () {
    this.model = {}
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get title () {
    return this.type === 'add' ? '职位添加' : '职位修改'
  }
  get fleetId () {
    return this.$route.query.fleetId
  }
  submitHandler (e : any) {
    e.preventDefault()
    this.$store.dispatch('addOrUpdateRecruit', this.model).then((response) => {
      if (response.success) {
        const toast = this['$createToast']({
          time: 3000,
          txt: '更新成功',
          type: 'txt'
        })
        toast.show()
        this.$router.push({ path: '/recruit-list' })
      } else {
        const toast = this['$createToast']({
          time: 1000,
          txt: '更新失败',
          type: 'txt'
        })
        toast.show()
      }
    })
  }
  submitClick () {
    return false
  }
  showDatePicker () {
    let date = this.model.departureTime
    let dateValue = new Date(date)
    this.dateTimePicker = this.$createDatePicker({
      title: '发车时间选择',
      min: new Date(2008, 7, 8, 8, 0, 0),
      max: new Date(2020, 9, 20, 20, 59, 59),
      value: dateValue,
      columnCount: 6,
      onSelect: this.dateSelectHandle,
      onCancel: this.dateCancelHandle
    })
    this.dateTimePicker.show()
  }
  dateSelectHandle (date : any) {
    console.log(date)
    let dateResult = this.getNowFormatDate(date)
    this.model['departureTime'] = dateResult
  }
  dateCancelHandle () {}
  getNowFormatDate (date : any) {
    let seperator1 = '-'
    let seperator2 = ':'
    let month = date.getMonth() + 1
    let strDate = date.getDate()
    let hours = date.getHours()
    let minutes = date.getMinutes()
    let seconds = date.getSeconds()
    if (month >= 1 && month <= 9) {
      month = '0' + month
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = '0' + strDate
    }
    if (hours >= 0 && hours <= 9) {
      hours = '0' + hours
    }
    if (minutes >= 0 && minutes <= 9) {
      minutes = '0' + minutes
    }
    if (seconds >= 0 && seconds <= 9) {
      seconds = '0' + seconds
    }
    let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + hours + seperator2 + minutes + seperator2 + seconds
    return currentdate
  }
}
