













import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import { getFleetList } from '@/store/modules/bus/actions'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class Home extends Vue {
  $createToast : any
  enterpriseList = []
  // 声明周期钩子
  page = 1
  limit = 10
  count = 0
  fleetId : any
  title: any
  created () {
    this.title = '企业列表'
  }
  activated () {
    this.enterpriseList = []
    this.getEnterpriseList({})
  }
  deactivated () {
    this.page = 1
    this.count = 0
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get showMore () {
    return Math.ceil(this.count / this.limit) >= (this.page + 1)
  }
  getEnterpriseList (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getEnterpriseList', { skip: skip, limit: limit }).then((response) => {
      if (response.success) {
        this.enterpriseList = this.enterpriseList.concat(response.data.list)
        this.count = response.data.count
      }
      toast.hide()
    })
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
    this.getEnterpriseList({ skip: skip })
  }
  toEnterpriseInfo (enterpriseInfo : any) {
    console.log('ddddd', enterpriseInfo)
    this.$router.push({ path: '/job-enterprise-info', query: { enterpriseId: enterpriseInfo.id } })
  }
  goTo (type : any) {
    this.$router.push({ path: '/' + type })
  }
}
