

























































import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class orderEditor extends Vue {
  $createToast : any
  $refs : any
  // 声明周期钩子
  start = ''
  end = ''
  page = 1
  limit = 10
  count = 0
  carNums = 0
  checkList = []
  orderList = []
  selectNum = 0
  selectCarId = ''
  // drawer数据
  drawerdata = [{}]
  selectedIndex = []
  openScreen = false
  actions = [
    {
      text: '已选择：0人'
    },
    {
      text: '请选车辆',
      type: 'button',
      action: 'selectCar'
    },
    {
      text: '确认分配',
      type: 'button',
      action: 'distributionOrder'
    }
  ]
  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get showMore () {
    return Math.ceil(this.count / this.limit) >= (this.page + 1)
  }
  get carId () {
    return this.$route.query.carId
  }
  get fleetId () {
    return this.$route.query.fleetId
  }
  created () {
    this.orderList = []
  }
  activated () {
    this.checkList = []
    this.selectNum = 0
    this.getOrderList({})
  }
  deactivated () {
    this.resetComponentInfo()
  }
  getOrderList (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    let carId = this.carId
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getOrderList', { carId: carId, skip: skip, start: this.start, end: this.end }).then((response) => {
      if (response.success) {
        if (obj.skip) {
          this.orderList = this.orderList.concat(response.data.list)
        } else {
          this.orderList = response.data.list
        }
        this.count = response.data.count
      }
      toast.hide()
    })
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
    this.getOrderList({ skip: skip })
  }
  orderChecked (obj : any) {
    let selectArr = obj.split('@')
    this.selectNum += parseInt(selectArr[1])
    this.actions[0].text = '已选择：' + this.selectNum + '人'
  }
  orderCancelChecked (obj : any) {
    let cancelArr = obj.split('@')
    this.selectNum -= parseInt(cancelArr[1])
    this.actions[0].text = '已选择：' + this.selectNum + '人'
  }
  clickHandler (obj : any) {
    if (obj.action === 'distributionOrder') {
      this.distributionOrder()
    } else if (obj.action === 'selectCar') {
      this.selectCar()
    }
  }
  selectCar () {
    this.getCarList({})
  }
  distributionOrder () {
    if (this.checkList.length <= 0) {
      const toast = this.$createToast({
        time: 1000,
        txt: '请选择订单',
        type: 'txt'
      })
      toast.show()
      return
    }
    if (!this.selectCarId) {
      const toast = this.$createToast({
        time: 1000,
        txt: '请选择要分配的车辆',
        type: 'txt'
      })
      toast.show()
      return false
    }
    this.orderAllocation()
  }
  getCarList (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    let fleetId = this.fleetId
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    console.log(this.fleetId, 'fleetId')
    this.$store.dispatch('getCarList', { fleetId: this.fleetId, skip: skip, limit: limit }).then((response) => {
      if (response.success) {
        if (!this.drawerdata[0]) {
          this.drawerdata = this.drawerdata.concat(response.data.list)
        } else {
          this.drawerdata = response.data.list
        }
        this.count = response.data.count
        this.$refs.drawer.show()
      }
      toast.hide()
    })
  }
  drawerSelect (carObj : any) {
    this.selectCarId = carObj.id
    this.actions[1].text = carObj.licensePlate ? '车牌：' + carObj.licensePlate : '车牌：未知'
    this.$refs.drawer.hide()
  }
  resetComponentInfo () {
    this.page = 1
    this.count = 0
    this.orderList = []
    this.selectCarId = ''
    this.actions[1].text = '请选车辆'
    this.actions[0].text = '已选择：0人'
  }
  orderAllocation () {
    if (this.checkList.length <= 0 || !this.selectCarId) {
      return
    }
    const toast = this['$createToast']({
      time: 0,
      txt: '订单分配中...'
    })
    toast.show()
    this.$store.dispatch('orderAllocation', { orderList: this.checkList, toCarId: this.selectCarId, carId: this.carId }).then((response) => {
      if (response.success) {
        const toast = this.$createToast({
          time: 1000,
          txt: '分配成功！',
          type: 'txt'
        })
        toast.show()
        this.resetComponentInfo()
        this.getOrderList({})
      }
      toast.hide()
    })
  }
  screenClick () {
    this.getOrderList({})
  }
  screenOpen () {
    this.openScreen = !this.openScreen
  }
  screenReset () {
    this.start = ''
    this.end = ''
  }
}
