













import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import { getFleetList } from '@/store/modules/bus/actions'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class Home extends Vue {
  $createToast : any
  recruitList = []
  // 声明周期钩子
  page = 1
  limit = 10
  count = 0
  fleetId : any
  title: any
  enterpriseId: any
  created () {
    this.title = '职位列表'
  }
  activated () {
    this.enterpriseId = this.$route.query.enterpriseId
    this.recruitList = []
    this.getRecruitList({})
  }
  deactivated () {
    this.page = 1
    this.count = 0
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get showMore () {
    return Math.ceil(this.count / this.limit) >= (this.page + 1)
  }
  getRecruitList (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getRecruitList', { enterpriseId: this.enterpriseId, skip: skip, limit: limit }).then((response) => {
      if (response.success) {
        this.recruitList = this.recruitList.concat(response.data.list)
        this.count = response.data.count
      }
      toast.hide()
    })
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
    this.getRecruitList({ skip: skip })
  }
  toRecruitInfo (recruitInfo : any) {
    this.$router.push({ path: '/recruit-edit', query: { recruitInfo: recruitInfo, type: 'edit' } })
  }
  goTo (type : any) {
    this.$router.push({ path: '/' + type, query: { enterpriseId: this.enterpriseId, type: 'add' } })
  }
}
