













import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import { getFleetList } from '@/store/modules/bus/actions'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class carInfo extends Vue {
  $createDialog : any
  $createToast : any
  carList = []
  // 声明周期钩子
  page = 1
  limit = 10
  count = 0
  carId : any = ''
  carNums = 0
  enterpriseInfo : any = {}
  enterpriseId: any
  activated () {
    this.enterpriseId = this.$route.query.enterpriseId
    // this.enterpriseInfo = this.$route.query.enterpriseInfo
    this.getEnterpriseInfo(this.enterpriseId)
  }
  deactivated () {}

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get showMore () {
    return Math.ceil(this.count / this.limit) >= (this.page + 1)
  }
  get fleetId () {
    return this.$route.query.fleetId
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
  }
  toenterpriseEdit () {
    this.$router.push({ path: '/job-enterprise-edit', query: { type: 'edit', enterpriseInfo: this.enterpriseInfo } })
  }
  toRecruit () {
    this.$router.push({ path: '/recruit-list', query: { enterpriseId: this.enterpriseInfo.id } })
  }
  getEnterpriseInfo (enterpriseId: String) {
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getEnterpriseInfo', { enterpriseId: enterpriseId }).then((response) => {
      if (response.success) {
        let data = response.data
        this.enterpriseInfo = data
      }
      toast.hide()
    })
  }
}
